import React from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
  Card,
  CardText,
  CardBody,
  CardHeader,
  CardImg,
} from 'reactstrap';
import { Helmet } from 'react-helmet';
import { Link } from 'react-router-dom';
import { errorMessages } from '../../../../../constants/messages';
import Loading from '../../../UI/Loading';
import Error from '../../../UI/Error';

import Heads from './img/Heads.png';
import Tails from './img/Tails.png';
import FlipGif from './img/FlipGif.gif';

import {DoACoinFlip} from '../../../../../actions/api';

var Coinf = function(){
  var result = DoACoinFlip();
  console.log('result:' + result)
  return result;
}
const CoinFlip = ({
  error,
  loading,
}) => {
  // Loading
  if (loading) return <Loading />;

  // Error
  if (error) return <Error content={error} />;

  let betAmount;
  let SelectedSide;

  var result;

  return (
    <div>
      <img src={FlipGif}/>
      {result}
      <button onClick={() => result = Coinf()}>
        Do a flip !
      </button>
    </div>
  );
};

CoinFlip.propTypes = {
  error: PropTypes.string,
  loading: PropTypes.bool.isRequired,
};

CoinFlip.defaultProps = {
  error: null,
};

export default CoinFlip;
