import { Firebase, FirebaseRef } from '../lib/firebase';


export const authenticatedRequest = function(method, url, body) {
    if (!Firebase.auth().currentUser) {
      throw new Error('Not authenticated. Make sure you\'re signed in!');
    }
    // Get the Firebase auth token to authenticate the request
    return Firebase.auth().currentUser.getIdToken().then(function(token) {
      var request = {
        method: method,
        headers: {
            'Authorization': 'Bearer ' + token
          }, 
      };
  
      if (method === 'POST') {
        request.contentType = 'application/json';
        request.data = JSON.stringify(body);
      }
  
      console.log('Making authenticated request:', method, url);
      console.log(request)
      return fetch(url, request).catch(function() {
        throw new Error('Request error: ' + method + ' ' + url);
      });
    });
  };


export const DoACoinFlip = function(){
    authenticatedRequest('POST', 'https://us-central1-kasino-w.cloudfunctions.net/api/coinflips', {coinflip: {score: 0, result: 'new'}}).then(function(response) {
        console.log('response' + JSON.stringify(response))
        return response;
    }.bind(this)).catch(function(error) {
        console.log('Error creating coinflip');
    throw error;
    });
}